
import Card from '../Card/Card';
import "./list.scss"
import React, { useState, useEffect } from 'react'

import database from '../../Data';

function List(props) {

  const data = database
  const [newdata, setNewdata] = useState(data)


  useEffect(
    () => {

      
      setNewdata(data.filter(e => props.selectedCats.includes(e.category) && e.price < props.maxPrice).sort((a, b) => (props.sort === 'asc' ? a.price - b.price : b.price - a.price)))
    }, [props.selectedCats, props.maxPrice, props.sort]
  )



  console.log(props.selectedCats + 'the price is ' + props.maxPrice + JSON.stringify(newdata) + 'my new list' + props.sort)



  return (
    <div className='list'>

{newdata.map((item) => (

<Card key={item.id} item={item} />



)

)}

    </div>
  )
}

export default List

